
import Tab from './components/tab/tab.jsx'
import Lottery from './lottery'
import Game from './components/game/game.jsx'
import './App.css';

// 图片
import trashIcon from './images/trash.png'


function App() {
  const games = [new Lottery('大乐透',[{maxNum:36,minSelect:6},{maxNum:15,minSelect:1}],2)];

  
  

//数字彩tab的渲染函数
  const gameItem = games.map(game=>{
    return<Tab name={game.name} />
  })




  return (
    <div className="App">
      <header class='tab-bar'>{gameItem}</header>
      
      
      <Game game={games[0]} />
    <footer class='footer' >
      <img src={trashIcon} alt='delete' ></img>
      
      <div>共2注, 供4元</div>
      <img src={trashIcon} alt='delete' ></img>
    </footer>
    </div>
  );
}

export default App;
