import {useState} from 'react'
import Ball from '../ball/ball'
import './game.css';
function Game(props){
    const [areas, setAreas] = useState([]);

    // 数字彩渲染区域  e.g 数字彩的前区后区
const renderLotteryArea = (game)=>{
 
    let areas = []
    game.area.forEach(el =>{
      console.log(el)
        areas.push(<div class='balls'>{renderBalls(el)}</div> )
    })
    return areas;
  }
  
  //数字彩号码选择渲染函数
    const renderBalls = (area)=>{
     
      let ballList = [];
      let maxNum = 1
      while(maxNum <= area.maxNum){
       
        ballList.push(<Ball num={maxNum} />)
        maxNum = maxNum+1
      }
      
     return ballList;
    }


    return <div class ='selection-area'>
    {renderLotteryArea(props.game)}

    </div>
}
export default Game;